@include b(Detalle) {
  @include e(header) {
    height: $Detalle-height;
    overflow: hidden;
    position: relative;

    @include bem-element(image) {
      @include first-child() {
        padding-left: 50px;
        @extend .container;
        margin: auto;
      }
      @include last-child() {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
        filter: blur(9px);
      }
    }
  }
  @include e(body) {
    @extend .container;
    padding-top: 20px;
  }
  @include e(title) {

  }
  @include e(category) {

  }
  @include e(where) {

  }
  @include e(description) {
  }
  @include e(image) {
    width: 100%;
    height: $Detalle-height;
    background-size: cover;
  }
  @include e(thumbnail) {

  }
  @include e(title) {
    color: orange;
  }
  @include e(fecha) {

  }
  @include e(caracteristicas){
    margin-top: 20px;
  }
  @include e(box){
    position: relative;
  }
}