@include b(Form) {

  @include e(group) {
    margin-bottom: 10px;
    @include last-child(){
      margin-bottom: 0;
    }
  }
  @include e(control) {
    padding-left: 0.5rem;
    width: 100%;
    background-color: white;
    color: #16325c;
    border: 1px solid #d8dde6;
    border-radius: 0.25rem;
    width: 100%;
    transition: border 0.1s linear, background-color 0.1s linear;
    display: inline-block;
    line-height: 1.875rem;
    min-height: calc(1.875rem + (1px * 2));
    @include m(select) {

    }
    @include m(textarea){
      width:inherit;
    }
  }
}