@include b(Btn) {
  display: inline-block;
  padding: 9px 12px;
  font-size: 14px;
  line-height: 14px;
  border-radius: 4px;
  background: #ffffff;
  color: #333333;
  cursor: pointer;
  border: 0px;
  margin: 3px 1px;
  transition: 0.5s;
  @include box-shadow(0px 2px 4px rgba(0, 0, 0, 0.125));

  @include hover-focus-active() {
    text-decoration: none;
  }
  @include m(red) {
    background: $red;
    color: #ffffff;
    @include hover-focus-active(){
      background: #f52222;
      color: #ffffff;
    }
  }
  @include m(info){
    background-color: $blue;
    color: white;
  }
}