@include b(Anuncio) {
  @extend .Grilla;

  grid-gap: 0;
  background: $Anuncio-background;
  @include box-shadow(0px 5px 30px -20px black);
  padding-bottom: 20px;

  @include hover-focus-active() {
    background: $Anuncio-background-hover;
  }
  @include e(header) {
    grid-column: span 12;
  }
  @include e(image) {
    width: 100%;
    height: 100%;
  }

  @include e(title) {
    grid-column: span 12;
    padding-left: $Anuncio-padding-left;
    padding-right: $Anuncio-padding-right;
    @include truncate();
    margin-top: 10px;
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    color: orange;
    @include hover-focus-active() {
      text-decoration: none;
      color: orange;
    }
  }
  @include e(link) {
    color: inherit;
    @include hover() {
      color: inherit;
      text-decoration: none;
    }
  }
  @include e(subtitle) {
    @extend .Anuncio__title;
    font-size: 16px;
    color: #5bc0de;
  }
  @include e(price) {
    grid-column: span 12;
    padding-left: $Anuncio-padding-left;
    padding-right: $Anuncio-padding-right;
    color: $Anuncio-color-price;
    font-size: 21px;
  }

  @include e(date) {
    grid-column: span 12;
    padding-left: $Anuncio-padding-left;
    padding-right: $Anuncio-padding-right;
    text-align: right;
    color: $Anuncio-color-date;
    font-size: 10pt;
    font-style: oblique;
  }

  @include e(state) {
    grid-column: span 12;
    padding-left: $Anuncio-padding-left;
    padding-right: $Anuncio-padding-right;
    color: white;
  }

  @include e(content) {
    grid-column: span 12;
  }
  @include e(footer) {
    grid-column: span 12;
  }
}